import React, { Component } from "react";
import { Card, Select, Input, Button, Modal, Icon, Table, message } from "antd";
import LinkButton from "../../components/link-button";
import { reqProducts, reqSearchProducts, reqUpdateStatus, reqDeleteProduct } from "../../api";
import { PAGE_SIZE } from "../../utils/constants";

const Option = Select.Option;

export default class ProductHome extends Component {
  state = {
    total: 0,
    products: [],
    loading: false,
    searchName: "",
    searchType: "productName",
  };

  initColumns = () => {
    this.columns = [
      {
        title: "Product Name",
        // title: "商品名称",
        dataIndex: "name",
      },
      {
        title: "Product Description",
        // title: "商品描述",
        dataIndex: "desc",
      },
      {
        title: "Price",
        // title: "价格",
        dataIndex: "price",
        render: (price) => "$" + price,
      },
      {
        width: 100,
        title: "Status",
        // title: "状态",
        // dataIndex: "status",
        render: (product) => {
          const { status, _id } = product;
          const newStatus = status === 1 ? 2 : 1;
          return (
            <span>
              <Button type="primary" style={{ width: "100%" }} onClick={() => this.updateStatus(_id, newStatus)}>
                {status === 1 ? "Not To Sale" : "To Sale"}
                {/* {status === 1 ? "下架" : "上架"} */}
              </Button>
              <div style={{ textAlign: "center" }}>
                <span>{status === 1 ? "On Sale" : "Not on Sale"}</span>
                {/* <span>{status === 1 ? "在售" : "已下架"}</span> */}
              </div>
            </span>
          );
        },
      },
      {
        width: 100,
        title: "Operation",
        // title: "操作",
        // dataIndex: "status",
        render: (product) => {
          return (
            <span>
              <LinkButton
                onClick={() => {
                  this.props.history.push("/product/detail", { product });
                }}>
                Details
                {/* 详情 */}
              </LinkButton>
              <LinkButton
                onClick={() => {
                  this.props.history.push("/product/addupdate", product);
                }}>
                Modify
                {/* 修改 */}
              </LinkButton>
              <LinkButton onClick={() => this.deleteProduct(product)}>delete</LinkButton>
              {/* <LinkButton onClick={() => this.deleteProduct(product)}>删除</LinkButton> */}
            </span>
          );
        },
      },
    ];
  };

  /*
  删除指定产品
   */
  deleteProduct = (product) => {
    Modal.confirm({
      title: `confirm to delete ${product.name}?`,
      // title: `确认删除${product.name}吗?`,
      onOk: async () => {
        const result = await reqDeleteProduct(product._id);
        if (result.status === 0) {
          message.success("Delete Product successfully!");
          // message.success("删除产品成功!");
          this.getProducts(1);
        }
      },
    });
  };

  getProducts = async (pageNum) => {
    this.pageNum = pageNum;
    this.setState({ loading: true });
    const { searchName, searchType } = this.state;
    let result = {};
    // console.log(searchName);
    if (searchName) {
      result = await reqSearchProducts(pageNum, PAGE_SIZE, searchName, searchType);
      // console.log("search", result);
    } else {
      result = await reqProducts(pageNum, PAGE_SIZE);
    }
    this.setState({ loading: false });

    // console.log(result);
    if (result.status === 0) {
      const { total, list } = result.data;
      this.setState({
        total,
        products: list,
      });
    }
  };
  updateStatus = async (productId, status) => {
    const result = await reqUpdateStatus(productId, status);
    if (result.status === 0) {
      message.success("update product successfully!");
      this.getProducts(this.pageNum);
    }
  };

  componentWillMount() {
    this.initColumns();
  }
  componentDidMount() {
    this.getProducts(1);
  }
  render() {
    const { products, total, loading, searchName, searchType } = this.state;
    const title = (
      <span>
        <Select value={searchType} style={{ width: 150 }} onChange={(value) => this.setState({ searchType: value })}>
          <Option value="productName">Search On Name</Option>
          {/* <Option value="1">按名称搜索</Option> */}
          <Option value="productDesc">Search On Desc</Option>
          {/* <Option value="2">按描述搜索</Option> */}
        </Select>
        <Input placeholder="Keyword" style={{ width: 150, margin: "0 15px" }} value={searchName} onChange={(event) => this.setState({ searchName: event.target.value })}></Input>
        {/* <Input placeholder="请输入关键字" style={{ width: 150, margin: "0 15px" }}></Input> */}
        <Button type="primary" onClick={() => this.getProducts(1)}>
          Search
        </Button>
        {/* <Button type="primary">搜索</Button> */}
      </span>
    );
    const extra = (
      <span>
        <Button
          type="primary"
          onClick={() => {
            this.props.history.push("/product/addupdate");
          }}>
          <Icon type="plus"></Icon>Add Product
          {/* <Icon type="plus"></Icon>添加商品 */}
        </Button>
      </span>
    );

    return (
      <Card title={title} extra={extra}>
        <Table
          bordered
          loading={loading}
          dataSource={products}
          columns={this.columns}
          rowKey="_id"
          pagination={{ current: this.pageNum, defaultPageSize: PAGE_SIZE, total, showQuickJumper: true, onChange: this.getProducts }}></Table>
      </Card>
    );
  }
}
