import ajax from "./ajax";
import { message } from "antd";
import jsonp from "jsonp";

// const BASE = "http://localhost:5000";
const BASE = "";

export const reqLogin = (username, password) => ajax(BASE + "/login", { username, password }, "POST");

export const reqAddUser = (user) => ajax(BASE + "/manage/user/add", user, "POST");

/* get categorys/subCategorys */
export const reqCategorys = (parentId) => ajax(BASE + "/manage/category/list", { parentId });

export const reqCategory = (categoryId) => ajax(BASE + "/manage/category/info", { categoryId });

/* add category */
export const reqAddCategory = ({ parentId, categoryName }) => ajax(BASE + "/manage/category/add", { parentId, categoryName }, "POST");

/* update category */
export const reqUpdateCategory = ({ categoryId, categoryName }) => ajax(BASE + "/manage/category/update", { categoryId, categoryName }, "POST");

export const reqProducts = (pageNum, pageSize) => ajax(BASE + "/manage/product/list", { pageNum, pageSize });

export const reqSearchProducts = (pageNum, pageSize, searchName, searchType) => ajax(BASE + "manage/product/search", { pageNum, pageSize, [searchType]: searchName });

export const reqUpdateStatus = (productId, status) => ajax(BASE + "/manage/product/updateStatus", { productId, status }, "POST");

export const reqAddOrUpdateProduct = (product) => ajax(BASE + "/manage/product/" + (product._id ? "update" : "add"), product, "POST");

// 删除指定名称的图片
export const reqDeleteImg = (name) => ajax(BASE + "/manage/img/delete", { name }, "POST");

// 获取所有角色的列表
export const reqRoles = () => ajax(BASE + "/manage/role/list");
// 添加角色
export const reqAddRole = (roleName) => ajax(BASE + "/manage/role/add", { roleName }, "POST");
// 添加角色
export const reqUpdateRole = (role) => ajax(BASE + "/manage/role/update", role, "POST");

// 获取所有用户的列表
export const reqUsers = () => ajax(BASE + "/manage/user/list");
// 删除指定用户
export const reqDeleteUser = (userId) => ajax(BASE + "/manage/user/delete", { userId }, "POST");
// 添加/更新用户
export const reqAddOrUpdateUser = (user) => ajax(BASE + "/manage/user/" + (user._id ? "update" : "add"), user, "POST");

// 删除指role
export const reqDeleteRole = (roleId) => ajax(BASE + "/manage/role/delete", { roleId }, "POST");

// 删除指product
export const reqDeleteProduct = (productId) => ajax(BASE + "/manage/product/delete", { productId }, "POST");

// 删除指category
export const reqDeleteCategory = (categoryId, parentId) => ajax(BASE + "/manage/category/delete", { categoryId, parentId }, "POST");

export const reqWeather = (city) => {
  return new Promise((resolve, reject) => {
    const url = `http://api.map.baidu.com/telematics/v3/weather?location=${city}&output=json&ak=3p49MVra6urFRGOT9s8UBWr2`;
    // 发送jsonp请求
    jsonp(url, {}, (err, data) => {
      // console.log("jsonp()", err, data);
      // 如果成功了
      if (!err && data.status === "success") {
        // 取出需要的数据
        const { dayPictureUrl, weather } = data.results[0].weather_data[0];
        resolve({ dayPictureUrl, weather });
      } else {
        // 如果失败了
        message.error("获取天气信息失败!");
      }
    });
  });
};
